<style lang="scss">
	.home-swiper {
		height: 100%;

		.swiper-item {
			height: 100%;
		}
	}
</style>

<template>
	<swiper class="home-swiper" @change="change" :current="listIndex">
		<swiper-item class="swiper-item" v-for="(item,index) in tab" :key="index">
			<list-item :list="listCatchData[index]"></list-item>
		</swiper-item>
	</swiper>
</template>

<script>
	import listItem from './list-item.vue'
	export default {
		name: "list",
		components: {
			listItem
		},
		props: {
			tab: {
				type: Array,
				default () {
					return []
				}
			},
			listIndex: {
				type: Number,
				default () {
					return 0
				}
			}
		},
		data() {
			return {
				listCatchData:{}
			};
		},
		watch:{
			tab(newVal){
				console.log('newVal',newVal);
				console.log(this.listIndex,'this.listIndex');
				if(newVal.length === 0) return
				this.getList(this.listIndex)
			}
		},
		// onLoad是用在页面，组件里只能用created
		created() {
			// this.getList(0)
			console.log(this.listIndex created,'this.listIndex');
		},
		methods: {
			change(e) {
				const {
					current
				} = e.detail
				this.$emit('change', current)
				console.log(this.tab[current].name)
				this.getList(current)
			},
			getList(current) {
				console.log("getList");
				this.$api.get_list({
					name:this.tab[current].name
				}).then(res => {
					const {
						data
					} = res
					// this.list = data
					//this.listCatchData[current] = data
					console.log('返回数据', data);
					this.$set(this.listCatchData,current,data)
					console.log(this.listCatchData);
				})
			}
		}
	}
</script>


<style lang="scss">
	.home-swiper {
		height: 100%;

		.swiper-item {
			height: 100%;
		}
	}
</style>

<template>
	<swiper class="home-swiper" @change="change" :current="listIndex">
		<swiper-item class="swiper-item" v-for="(item,index) in tab" :key="index">
			<list-item :list="list"></list-item>
		</swiper-item>
	</swiper>
</template>

<script>
	import listItem from './list-item.vue'
	export default {
		name: "list",
		components: {
			listItem
		},
		props: {
			tab: {
				type: Array,
				default () {
					return []
				}
			},
			listIndex: {
				type: Number,
				default () {
					return 0
				}
			}
		},
		data() {
			return {
				list: [],
			};
		},
		// onLoad是用在页面，组件里只能用created
		created() {
			this.getList('后端开发')
		},
		methods: {
			change(e) {
				const {
					current
				} = e.detail
				this.$emit('change', current)
				console.log(this.tab[current].name)
				this.getList(this.tab[current].name)
			},
			getList(name) {
				this.$api.get_list({
					name
				}).then(res => {
					const {
						data
					} = res
					this.list = data
				})
			}
		}
	}
</script>
